.App {
    top: 50%;
    left: 50%;
    color: white;
    width: 100vw;
    height: 100vh;
    position: absolute;
    transform: translate(-50%, -50%);
    pointer-events: all;
}

.Main {
    position: absolute;
    bottom: 0;

    display: flex;
    justify-content: flex-start;
    flex-direction: row;
}

.progress-ring__circle {
    transition: stroke-dashoffset 0.1s;
}
  